<template>
  <div class="content">
    <Modal v-model="addFieldModel" title="">
      <Form :model="formItem"  :rules="loginRules"  ref="FormRef" :label-width="125">
        <FormItem label=""  prop="filedName">
          <Input v-model.trim="formItem.filedName" placeholder=""></Input>
        </FormItem>
        <FormItem label="">
          <Select v-model="formItem.filedType" @on-change="delAddValue">
            <Option value="1"></Option>
            <Option value="2"></Option>
            <Option value="3"></Option>
            <Option value="4"></Option>
            <Option value="5"></Option>
            <Option value="6"></Option>
            <Option value="7"></Option>
          </Select>
        </FormItem>
        <FormItem label="">
          <RadioGroup v-model="formItem.isMust">
            <Radio label="1"></Radio>
            <Radio label="0"></Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="" v-if="formItem.filedType==='1'" prop="stringLength">
          <Input v-model.trim="formItem.stringLength" placeholder=""></Input>
        </FormItem>
        <!---->
        <FormItem label="" v-if="formItem.filedType==='2'" prop="min">
          <Input v-model.trim="formItem.min" placeholder=""></Input>
        </FormItem>
        <FormItem label="" v-if="formItem.filedType==='2'" prop="max">
          <Input v-model.trim="formItem.max" placeholder=""></Input>
        </FormItem>
        <!---->
        <FormItem label="" v-if="formItem.filedType==='3'" prop="dmin">
          <Input v-model.trim="formItem.min" placeholder=""></Input>
        </FormItem>
        <FormItem label="" v-if="formItem.filedType==='3'" prop="dmax">
          <Input v-model.trim="formItem.max" placeholder=""></Input>
        </FormItem>
        <FormItem label="" v-if="formItem.filedType==='3'" prop="decimalPoint">
          <Input v-model.trim="formItem.decimalPoint" placeholder=""></Input>
        </FormItem>
        <FormItem label="" v-if="formItem.filedType==='6'">
          <Input v-model.trim="formItem.rediaoType" placeholder=""></Input>
        </FormItem>
        <FormItem label="" v-if="formItem.filedType==='4'">
          <Input v-model.trim="formItem.singleSelect" placeholder=""></Input>
        </FormItem>
        <FormItem label="" v-if="formItem.filedType==='5'">
          <Input v-model.trim="formItem.multipleSelect" placeholder=""></Input>
        </FormItem>
        <FormItem label="" v-if="formItem.filedType==='7'">
          <Select v-model="formItem.timeLimit" style="width:200px">
            <Option :value="1">yyyy-MM-dd HH:mm:ss</Option>
            <Option :value="2">yyyy-MM-dd</Option>
          </Select>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="addField" type="primary"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
  name: 'editTableAddField',
  data () {
    const validateMax = (rule, value, callback) => {
      if (this.formItem.min !== undefined && this.formItem.max !== undefined && this.formItem.min !== '' && this.formItem.max !== '') {
        if (this.formItem.min * 1 > this.formItem.max * 1) {
          callback(new Error(''))
          this.$refs.FormRef.validateField('min')
        } else {
          callback()
          this.$refs.FormRef.validateField('min')
        }
      } else {
        callback()
      }
    }
    const validateMin = (rule, value, callback) => {
      if (this.formItem.min !== undefined && this.formItem.max !== undefined && this.formItem.min !== '' && this.formItem.max !== '') {
        if (this.formItem.min * 1 > this.formItem.max * 1) {
          callback(new Error(''))
        } else {
          callback()
        }
      } else {
        callback()
      }
    }
    return {
      tableList: [], // 
      activeList: 0,
      /*  */
      formItem: {
        filedName: '',
        filedType: '1',
        isMust: '1'
      },
      /*  */
      loginRules: {
        stringLength: [
          { pattern: /^[1-9]\d*$/, message: '', trigger: 'blur' }
        ],
        filedName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        max: [
          { pattern: /^-?[1-9]\d*$/, message: '', trigger: 'blur' },
          { validator: validateMax, trigger: 'blur' }
        ],
        min: [
          { pattern: /^-?[1-9]\d*$/, message: '', trigger: 'blur' },
          { validator: validateMin, trigger: 'blur' }
        ],
        dmax: [
          { pattern: /^-?[0-9]+([.]{1}[0-9]+){0,1}$/, message: '', trigger: 'blur' },
          { validator: validateMax, trigger: 'blur' }
        ],
        dmin: [
          { pattern: /^-?[0-9]+([.]{1}[0-9]+){0,1}$/, message: '', trigger: 'blur' },
          { validator: validateMin, trigger: 'blur' }
        ],
        decimalPoint: [
          { pattern: /^[1-9]\d*$/, message: '', trigger: 'blur' }
        ]
      },
      /*  */
      addFieldModel: false,
      /*  */
      constraint: 0,
      valid: false
    }
  },
  props: {
  },
  mounted () {
    /*  */
    this.delDefault()
  },
  methods: {
    /*  */
    addField () {
      /*  */
      let formItem = JSON.parse(JSON.stringify(this.formItem))
      if (formItem.filedType === '4') {
        formItem.selects = formItem.singleSelect.split(/[,;]/)
        formItem.singleSelect = formItem.selects.join(',')
      }
      if (formItem.filedType === '5') {
        formItem.selects = formItem.multipleSelect.split(/[,;]/)
        formItem.multipleSelect = formItem.selects.join(',')
      }
      if (formItem.filedType === '6') {
        formItem.selects = formItem.rediaoType.split(/[,;]/)
        formItem.rediaoType = formItem.selects.join(',')
      }
      /*  */
      switch (formItem.timeLimit) {
        case 1:
          formItem.timeLimit = 'yyyy-MM-dd HH:mm:ss'
          break
        case 2:
          formItem.timeLimit = 'yyyy-MM-dd'
          break
      }

      this.handleSubmit()

      if (this.valid) {
        this.addFieldModel = false
        this.delDefault()
        this.$emit('addArr', formItem)
      }
    },
    /*  */
    handleSubmit () {
      this.$refs['FormRef'].validate((valid) => {
        this.valid = valid
      })
    },
    /*  */
    delAddValue () {
      let filedName = this.formItem.filedName
      let filedType = this.formItem.filedType
      this.formItem = {
        filedName: filedName,
        filedType: filedType,
        isMust: '1'
      }
      this.$refs.FormRef.resetFields()
    },
    /*  */
    delDefault () {
      this.formItem = {
        filedName: '',
        filedType: '1',
        isMust: '1'
      }
    }
  }
}
</script>

<style scoped lang="scss">
.content{
  padding: 15px;
}
.tablelist{
  line-height: 40px;
  &:hover{
    background: #f6f6f6;
    cursor: pointer;
  }
  &.active{
    background: #57a3f3;
    color: white;
  }
}
</style>
